import clTabs from '@/components/cl-tabs/cl-tabs.vue';
import uniCard from '@/components/uni-card/uni-card.vue';
export default {
	components: {
		clTabs,
		uniCard
	},
	data() {
		return {
			show_text: '查看全部',
			card_text_show: false,
			tabBars: ['消息', '动态'],
			tabCurrentIndex: -1,
			sysWidth: 0,
			source: 'touch',
			fristTouch: false,
			cuIconList: [
				{
					cuIcon: 'cardboardfill',
					color: 'red',
					badge: 120,
					name: 'VR'
				},
				{
					cuIcon: 'recordfill',
					color: 'orange',
					badge: 1,
					name: '录像'
				},
				{
					cuIcon: 'picfill',
					color: 'yellow',
					badge: 0,
					name: '图像'
				},
				{
					cuIcon: 'noticefill',
					color: 'olive',
					badge: 22,
					name: '通知'
				},
				{
					cuIcon: 'upstagefill',
					color: 'cyan',
					badge: 0,
					name: '排行榜'
				},
				{
					cuIcon: 'clothesfill',
					color: 'blue',
					badge: 0,
					name: '皮肤'
				},
				{
					cuIcon: 'discoverfill',
					color: 'purple',
					badge: 0,
					name: '发现'
				},
				{
					cuIcon: 'questionfill',
					color: 'mauve',
					badge: 0,
					name: '帮助'
				},
				{
					cuIcon: 'commandfill',
					color: 'purple',
					badge: 0,
					name: '问答'
				},
				{
					cuIcon: 'brandfill',
					color: 'mauve',
					badge: 0,
					name: '版权'
				}
			],
			modalName: null,
			gridCol: 3,
			gridBorder: false,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null
		};
	},
	onLoad() {
		this.sysWidth = uni.getSystemInfoSync().screenWidth;

		this.fristTouch = true;

	},
	mounted() {
		this.tabCurrentIndex = 1;
	},
	methods: {
		scrolltolower() {
			console.log('111111111111');
		},
		tabChange(e) {
			this.tabCurrentIndex = e;
			console.log(e);
		},
		changeCurrent(e) {
			this.source = e.detail.source;
		},
		transition(e) {
			if (this.source == '') {
			} else {
				if (this.fristTouch) {
					this.$refs.tabs2.move(e.detail.dx - this.tabCurrentIndex * this.sysWidth);
				} else {
					this.$refs.tabs2.move(e.detail.dx);
				}
			}
		},
		animationfinish(e) {
			this.fristTouch = false;
			this.source = 'touch';
			this.tabCurrentIndex = e.detail.current;
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target;
		},
		hideModal(e) {
			this.modalName = null;
		},
		Gridchange(e) {
			this.gridCol = e.detail.value;
		},
		Gridswitch(e) {
			this.gridBorder = e.detail.value;
		},
		MenuBorder(e) {
			this.menuBorder = e.detail.value;
		},
		MenuArrow(e) {
			this.menuArrow = e.detail.value;
		},
		MenuCard(e) {
			this.menuCard = e.detail.value;
		},
		SwitchSex(e) {
			this.skin = e.detail.value;
		},
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX;
		},
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left';
		},
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target;
			} else {
				this.modalName = null;
			}
			this.listTouchDirection = null;
		}
	}
};
